const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const store = {
  currentPage: 1,
};

// ajax ÎèôÏûë ÏàòÌñâÏ≤òÎ¶¨ Ìï®Ïàò
function getData(url) {
  // Í∏ÅÏñ¥Ïò¨ ÌéòÏù¥ÏßÄÎ•º Ïò§Ìîà
  ajax.open('GET', url, false);
  // Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÏÜ°
  ajax.send();

  // Ìï®ÏàòÏ≤òÎ¶¨Ïùò Í≤∞Í≥ºÎ¨ºÏùÑ Î∞òÌôò
  return JSON.parse(ajax.response);
}

// Îâ¥Ïä§ Ï†úÎ™©
function newsFeed() {
// ul ÌÉúÍ∑∏ Ïïà Ï™ΩÏóê li a ÌÉúÍ∑∏Î•º 10Î¨∂Ïùå Ï≤òÎ¶¨ Ìï¥ÏïºÌïòÍ∏∞ ÎïåÎ¨∏Ïóê Î∞∞Ïó¥ Ïù¥Ïö©
const newsList = [];

const newsFeed = getData(NEWS_URL);

// Î∞∞Ïó¥ ÏïàÏóê Î®ºÏ†Ä ulÌÉúÍ∑∏ ÏÇΩÏûÖ
newsList.push('<ul>');

for(let i = (store.currentPage - 1) * 10; i < currentPage * 10; i++) {
  // Î™©Î°ù UI
  newsList.push(`
    <li>
      <a href="#/show/${newsFeed[i].id}">
      ${newsFeed[i].title} ${newsFeed[i].comments_count}
      </a>
    </li>
  `);
}
// ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ui
newsList.push(`
  <div>
    <a href="#/page/${store.currentPage - 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
    <a href="#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
  </div>
`);

newsList.push('</ul>');
// ÎçÆÏñ¥ÏîåÏö∞Í∏∞
container.innerHTML = newsList.join('');
};





// Îâ¥Ïä§ ÎÇ¥Ïö©
function newsDetail() {
  // hashÎ•º ÏïåÏïÑÎÇ¥Í∏∞(Îß® ÏïûÏùò #Ï†úÍ±∞Î≤ÑÏ†º)
  const id = location.hash.substring(1);
  
  // content_url Î≥ÄÏàòÏóê ÏûàÎäî @id(ÏûÑÏãú)Î•º ÏïåÏïÑÎÇ∏ idÍ∞íÏúºÎ°ú ÎåÄÏ≤¥
  const newsContent = getData(CONTENT_URL.replace('@id', id));
  const title = document.createElement('h1');
  
  // Í∏Ä ÎÇ¥Ïö© UI
  container.innerHTML = `
  <h1>${newsContent.title}</h1>
  
  <div>
    <a href="#">Î™©Î°ùÏúºÎ°ú</a>
  </div>
  `;
}

// ÌôîÎ©¥ Ï†ÑÌôòÏùÑ ÏúÑÌïú router
function router() {
  // hash ÏïåÏïÑÎÇ¥Í∏∞
  const routePath = location.hash;

  // routePathÍ∞Ä ÎπÑÏñ¥ÏûàÏùÑ Îïå(hashÍ∞Ä ÎπÑÏñ¥ÏûàÏùÑ Îïå) === Ï≤´ ÏßÑÏûÖ
  // 'Î™©Î°ùÏúºÎ°ú' ÎòêÌïú hashÍ∞Ä ÎπÑÏñ¥ÏûàÍ∏∞ ÎïåÎ¨∏Ïóê newsFeedÎ°ú Ïó∞Í≤∞Îê®
  // location.hashÏùò Í∞íÏóê #Îßå Ï°¥Ïû¨ÌïòÎ©¥ Îπà Í∞íÏúºÎ°ú Ï≤òÎ¶¨Îê®
  if (routePath === '') {
    // Îâ¥Ïä§ Ï†úÎ™© Í∞ÄÏ†∏Ïò§Í∏∞
    newsFeed();
    // Ìï¥Îãπ Î¨∏ÏûêÏó¥Ïù¥ ÏûàÏúºÎ©¥ 0Ïù¥ÏÉÅÏùò ÏúÑÏπòÍ∞í, ÏóÜÏúºÎ©¥ -1 Î∞òÌôò
  } else if (routePath.indexOf('#/page/') >= 0) { 
    store.currentPage = Number(routePath.substring(7));
    newsFeed();
  } else {
    // Îâ¥Ïä§ Í∏Ä Í∞ÄÏ†∏Ïò§Í∏∞
    newsDetail();
  }
};

// windowÍ∞ùÏ≤¥
// hashchangeÎ•º ÌôîÎ©¥Ï†ÑÌôòÏùÑ ÏúÑÌïú Ìä∏Î¶¨Í±∞Î°ú ÏÇ¨Ïö©(routerÎ•º Ïó∞Í≤∞)
window.addEventListener('hashchange', router);

router();